    $primaryGreen: #C2DC55;
    $secondaryGreen: #58985F;
    $darkGreen: #285E4D;
    $teal: #6CD3C2;
    $purple: #9F87E8;
    $yellow: #FDD504;
    $peach: #FF8080;
    $black: #333333;
    $grey: #6C6C6C;
    $silver: #ABABAB;
    $subtle: #CCCCCC;
    $light: #E5E5E5;
    $lighter: #F5F5F5;
    $lightest: #FAFAFA;
    $white: #FFFFFF;
    $indianEquity: #294C88;
    $debt: #00A6A4;
    $internationalEquity: #F97B30;
    $gold: #FAAD3B;
    $cash: #74BA85;
    $highestRisk: #FFE7E5;
    $secondHeighestRisk: #FFF1DD;
    $moderateRisk: #FFFBD3;
    $conservativeRisk: #F0F6D5;
    $lowestRisk: #EDFAFF;
    $red: #FF8080;
    $lightGreenBackground: #F1FCF6;
    $darkYellow: #F3B92A;

    * {
        font-family: 'DM Sans';
    }

    a {
        color: $secondaryGreen;
    }

    .mint-link {
        color: $secondaryGreen;

        :hover {
            color: $darkGreen;
        }
    }

    .custom-form-card {
        position: relative;
        display: flex;
        flex-direction: column;
        min-width: 0;
        word-wrap: break-word;
        background-color: #F5F5F5;
        background-clip: border-box;
        border: 1px solid $lightest;
        border-radius: 4px;
    }

    .rupee {
        background-image: url("https://d3etfstcuyoos5.cloudfront.net/app-images/imgs/rupee.webp");
        padding-left: 25px;
        background-size: 11px;
        background-repeat: no-repeat;
        background-position-y: 11px;
        background-position-x: 10px;
    }

    .solidCircleNumber {
        width: 24px;
        height: 24px;
        background-color: $darkGreen;
        color: $primaryGreen;
        font-size: 14px;
        font-weight: 700;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 10;
    }

    .formProgressBar {
        position: relative;
        top: -1.25rem;
        left: -2.25rem;
        margin-right: -4.5rem;
    }

    @media (min-width: 576px) {
        .formProgressBar {
            top: -1.5rem;
            left: -2.5rem;
            margin-right: -5rem;
        }
    }

    .normalInput {
        outline: none;
        border: none;
        border-bottom: 1px solid var(--light);
        margin-top: 2%;
        margin-bottom: 4%;
        font-size: 24px;
        font-weight: 500;
        width: 100%;
        caret-color: var(--secondaryGreen);
        background: transparent;
    }

    .normalInput:focus {
        border-bottom: 1px solid var(--secondaryGreen);
    }

    .normalInput::placeholder {
        font-size: 24px;
        font-weight: 500;
        color: var(--silver);
    }

    .normalInput.rupee {
        background-image: url("https://d3etfstcuyoos5.cloudfront.net/app-images/imgs/rupee.webp");
        padding-left: 20px;
        background-size: 16px;
        background-repeat: no-repeat;
        background-position-y: 9px;
    }

    .normalInput:disabled {
        color: var(--black);
        background-color: transparent
    }